import * as THREE from "three";
import React, { useEffect, Component } from "react";
import ReactDOM from "react-dom";
import { render } from "@testing-library/react";
import { Model } from "../Models/ModelDisplay";
import { OrbitControls, Box } from "@react-three/drei/native";
import { ARCanvas } from "react-xr";

const ThreeJSModelViewer = () => {
  return (
    <ARCanvas>
      <ambientLight />
      <spotLight />

      <OrbitControls />

      <Box position={[0, 0.8, -1]} scale={[1, 1, 1]}>
        <meshStandardMaterial color="#e23" />
      </Box>
    </ARCanvas>
  );
};

export default ThreeJSModelViewer;
